
.frame {
  position: relative;
  float: left;
  /* width: 63mm; */
  width: 64.89mm;
  /* height: 88mm; */
  height: 90.64mm;
  margin: 0px 1px;
}

.card {
  position: absolute;
  width: 64.89mm;
  height: 90.64mm;
  z-index: 10;
}

.input {
  position: absolute;
  left: 5px;
  bottom: 7px;
  z-index: 20;
  width: 55mm;
}

@media print{
    .input{
        display: none;
    }
}

.printFrame {
  width: 770px;
}

